// Desktop first

@mixin vp-1366 {
  @media (min-width: $vp-1366) {
    @content;
  }
}

@mixin desktop-only {
  @media (min-width: $vp-1024) {
    @content;
  }
}

@mixin max-tablet {
  @media (max-width: $vp-1023) {
    @content;
  }
}

@mixin max-mobile {
  @media (max-width: $vp-767) {
    @content;
  }
}

